/* SchedulePage.css */

.schedule-page {
    display: flex;
    justify-content: space-between; /* 왼쪽 검색 결과와 오른쪽 시간표를 분리 */
    padding: 20px;
}

.subjects-table, .timetable {
    border-collapse: collapse;
    width: 48%; /* 테이블 폭 설정 */
    margin: 10px 0;
}

.subjects-table th, .subjects-table td, .timetable th, .timetable td {
    border: 1px solid #ccc; /* 셀 경계 표시 */
    text-align: center; /* 텍스트 중앙 정렬 */
    padding: 8px;
}

.subjects-table th, .timetable th {
    background-color: #f4f4f4; /* 헤더 배경색 */
}

/* 선택된 과목 박스 스타일 */
.selected-subjects {
    border: 1px solid #ccc;
    padding: 10px;
    width: 100%;
    margin-top: 20px;
}

.selected-subjects div {
    margin-bottom: 5px;
}

/* 시간표 스타일 */
.timetable {
    width: 48%; /* 시간표 폭 설정 */
}

.timetable td {
    height: 50px; /* 각 시간표 셀의 높이 */
}
